import { faBookOpen } from "@fortawesome/free-solid-svg-icons";
import React, { useState } from "react";
import { useEffect } from "react";
import { RouteComponentProps } from "react-router";
import styled from "styled-components";
import Spell, {
  SpellDuration,
  SpellRange,
  SpellRite,
  SpellSchool,
  SpellSource,
} from "../../../../data/Spell";
import NumberField from "../../../form_elements/NumberField";
import SelectField from "../../../form_elements/SelectField";

import StringField from "../../../form_elements/StringField";
import TextField from "../../../form_elements/TextField";

const SpellBuilderView = () => {
  //Source, Rite, Casting Time, Duration, Range, School
  const [drain, setDrain] = useState<number[]>([0, 3, 2, 1, 1, 1, 0, 0, 0, 0]);
  const [spell, onEdit] = useState<Spell>(new Spell());

  const makeDrain = (): number => {
    let sum = 0;
    drain.forEach((d) => {
      sum += d;
    });
    return sum;
  };

  useEffect(() => {
    console.log(drain, makeDrain());
  }, [drain]);

  return (
    <CenterWrapper>
      <View>
        <StringField
          value={spell.name}
          label="Name"
          onChange={(name) => onEdit({ ...spell, name: name })}
        />
        <NumberField value={makeDrain()} label="Drain" onChange={(drain) => undefined} />
        <SelectField
          value={{ value: spell.source, label: spell.source }}
          options={[
            { value: SpellSource.air, label: SpellSource.air },
            { value: SpellSource.arcane, label: SpellSource.arcane },
            { value: SpellSource.demonic, label: SpellSource.demonic },
            { value: SpellSource.divine, label: SpellSource.divine },
            { value: SpellSource.druidic, label: SpellSource.druidic },
            { value: SpellSource.earth, label: SpellSource.earth },
            { value: SpellSource.fire, label: SpellSource.fire },
            { value: SpellSource.frost, label: SpellSource.frost },
            { value: SpellSource.psychic, label: SpellSource.psychic },
          ]}
          label={"Source"}
          onChange={(category: string) => {
            setDrain((d) => {
              let newD = [...d];
              newD[0] = category === SpellSource.arcane ? 1 : 0;
              return newD;
            });
            onEdit({ ...spell, source: category });
          }}
        />
        <SelectField
          value={{ value: spell.castTime, label: spell.castTime }}
          options={[
            { value: "4 - Mystb Rounds", label: "4 - Mystb Rounds" },
            { value: "9 + 3", label: "9 + 3" },
            { value: "9 + 3 (Channel)", label: "9 + 3 (Channel)" },
            { value: "7 + 3", label: "7 + 3" },
            { value: "5 + 3", label: "5 + 3" },
            { value: "5", label: "5" },
            { value: "Drain (Channel)", label: "Drain (Channel)" },
            { value: "Drain + 3", label: "Drain + 3" },
          ]}
          label={"Casting Time"}
          onChange={(value: string) => {
            setDrain((d) => {
              let newD = [...d];
              switch (value) {
                case "4 - Mystb Rounds":
                  newD[2] = 2;
                  break;
                case "9 + 3":
                  newD[2] = -4;
                  break;
                case "9 + 3 (Channel)":
                case "Drain (Channel)":
                  newD[2] = -3;
                  break;
                case "7 + 3":
                  newD[2] = -1;
                  break;
                case "5 + 3":
                  newD[2] = 1;
                  break;
                case "5":
                  newD[2] = 4;
                  break;
                case "Drain":
                  newD[2] = -2;
                  break;
                default:
                  newD[2] = 0;
                  break;
              }
              return newD;
            });
            onEdit({ ...spell, castTime: value });
          }}
        />
        <SelectField
          value={{ value: spell.rite, label: spell.rite }}
          options={[
            { value: SpellRite.mental, label: SpellRite.mental },
            { value: SpellRite.ritual, label: SpellRite.ritual },
            { value: SpellRite.somatic, label: SpellRite.somatic },
            { value: SpellRite.verbal, label: SpellRite.verbal },
          ]}
          label={"Rite"}
          onChange={(category: string) => {
            setDrain((d) => {
              let newD = [...d];
              switch (category) {
                case SpellRite.mental:
                case SpellRite.ritual:
                  newD[1] = 3;
                  break;
                case SpellRite.somatic:
                  newD[1] = 1;
                  break;
                case SpellRite.verbal:
                  newD[1] = 2;
                  break;
                default:
                  newD[1] = 0;
                  break;
              }
              return newD;
            });
            onEdit({ ...spell, rite: category });
          }}
        />
        <SelectField
          value={{ value: spell.duration, label: spell.duration }}
          options={[
            { value: SpellDuration.concentration, label: SpellDuration.concentration },
            { value: SpellDuration.fixed, label: SpellDuration.fixed },
            { value: SpellDuration.instantaneous, label: SpellDuration.instantaneous },
            { value: SpellDuration.permanent, label: SpellDuration.permanent },
          ]}
          label={"Duration"}
          onChange={(value: string) => {
            setDrain((d) => {
              let newD = [...d];
              switch (value) {
                case SpellDuration.concentration:
                  newD[3] = 0;
                  break;
                case SpellDuration.fixed:
                  newD[3] = -1;
                  break;
                case SpellDuration.instantaneous:
                case SpellDuration.permanent:
                  newD[3] = 1;
                  break;
                default:
                  newD[3] = 0;
                  break;
              }
              return newD;
            });
            onEdit({ ...spell, duration: value });
          }}
        />
        {spell.duration === SpellDuration.fixed && (
          <StringField
            value={spell.durationText}
            label="Duration"
            onChange={(value) => onEdit({ ...spell, durationText: value })}
          />
        )}
        <SelectField
          value={{ value: spell.range, label: spell.range }}
          options={[
            { value: SpellRange.touch, label: SpellRange.touch },
            { value: SpellRange.sonic, label: SpellRange.sonic },
            { value: SpellRange.radius, label: SpellRange.radius },
            { value: SpellRange.radiusPlus, label: SpellRange.radiusPlus },
            { value: SpellRange.losAura, label: SpellRange.losAura },
            { value: SpellRange.losManipulate, label: SpellRange.losManipulate },
            { value: SpellRange.losPoint, label: SpellRange.losPoint },
          ]}
          label={"Range"}
          onChange={(value: string) => {
            setDrain((d) => {
              let newD = [...d];
              switch (value) {
                case SpellRange.touch:
                case SpellRange.sonic:
                case SpellRange.radius:
                case SpellRange.losAura:
                  newD[4] = 1;
                  break;
                case SpellRange.radiusPlus:
                  newD[4] = 2;
                  break;
                case SpellRange.losManipulate:
                  newD[4] = 3;
                  break;
                case SpellRange.losPoint:
                  newD[4] = 4;
                  break;
                default:
                  newD[4] = 0;
                  break;
              }
              return newD;
            });
            onEdit({ ...spell, range: value });
          }}
        />
        <SelectField
          value={{ value: spell.school, label: spell.school }}
          options={[
            { value: SpellSchool.conjuration, label: SpellSchool.conjuration },
            { value: SpellSchool.evocation, label: SpellSchool.evocation },
            { value: SpellSchool.fortification, label: SpellSchool.fortification },
            { value: SpellSchool.hex, label: SpellSchool.hex },
            { value: SpellSchool.illusion, label: SpellSchool.illusion },
            { value: SpellSchool.necromancy, label: SpellSchool.necromancy },
            { value: SpellSchool.transmutation, label: SpellSchool.transmutation },
            { value: SpellSchool.enchantment, label: SpellSchool.enchantment },
          ]}
          label={"School"}
          onChange={(value: string) => {
            setDrain((d) => {
              let newD = [...d];
              switch (value) {
                case SpellSchool.fortification:
                case SpellSchool.hex:
                case SpellSchool.illusion:
                case SpellSchool.necromancy:
                case SpellSchool.transmutation:
                case SpellSchool.conjuration:
                  newD[4] = 1;
                  break;
                case SpellSchool.evocation:
                  newD[4] = 0;
                  break;
                case SpellSchool.enchantment:
                  newD[4] = 2;
                  break;
                default:
                  newD[4] = 0;
                  break;
              }
              return newD;
            });
            onEdit({ ...spell, school: value });
          }}
        />
        <TextField
          value={spell.effect}
          label="Effect"
          icon={faBookOpen}
          onChange={(value) => onEdit({ ...spell, effect: value })}
        />
        <TextField
          value={spell.damage}
          label="Damage"
          icon={faBookOpen}
          onChange={(value) => onEdit({ ...spell, damage: value })}
        />
        <TextField
          value={spell.mastery}
          label="Mastery"
          icon={faBookOpen}
          onChange={(value) => onEdit({ ...spell, mastery: value })}
        />
        <TextField
          value={spell.resist}
          label="Resist"
          icon={faBookOpen}
          onChange={(value) => onEdit({ ...spell, resist: value })}
        />
      </View>
    </CenterWrapper>
  );
};

export default SpellBuilderView;

const CenterWrapper = styled.div`
  overflow: hidden;
  width: 100%;
  height: 100%;
`;

const View = styled.div`
  color: ${({ theme }) => theme.tile.color};
  font-size: 16px;
  padding: 5px;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;

  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
`;
